














import { Vue, Component, Prop } from 'vue-property-decorator';
import { Columns } from '@/types/columns';
import ColumnInfo from './ColumnInfo.vue';
import Card from '@commonComponents/Card.vue';
import BaseImage from '@commonComponents/BaseImage.vue';
import RouteName from '@router/routerName';

function getCount(articles: Columns = []): number {
  let count = 0;

  for (const article of articles) {
    if (article.articles) {
      count += getCount(article.articles);
    } else if (article.content) {
      count += 1;
    }
  }

  return count;
}

@Component({
  name: 'ArticleCard',
  components: {
    Card,
    BaseImage,
    ColumnInfo,
  },
})
export default class extends Vue {
  @Prop({ type: String, required: true }) private id!: string;
  @Prop({ type: String, default: '' }) private title!: string;
  @Prop({ type: String, default: '' }) private banner!: string;
  @Prop({ type: Array, default: () => ([]) }) private articles!: Columns;

  private RouteName = RouteName;

  private get count(): number {
    return getCount(this.articles);
  }
}
